<template>
  <div>
    <div>
      <Radar v-if="loaded" :data="chartData" :options="radarChartOptions" class="auto-height neu-" />
    </div>

    <!-- <div class="wrap-chart">
      <Radar v-if="loaded" :data="mathChartData" :options="radarChartOptions" />
    </div> -->

  </div>
</template>
<script>
import axios from "axios";
import { Radar } from "vue-chartjs";
import { Chart, registerables } from "chart.js";

Chart.register(...registerables);

export default {
  name: "RadarChart",
  components: { Radar },

  props: {
    selectedStudentName: {
      type: String,
      default: "",
      required: true,
    },
  },

  watch: {
    selectedStudentName(newSelectedStudentName) {
      console.log("raDar.vue selectedStudentName: ", newSelectedStudentName);
      this.getStudentData();
    },
  },

  data: () => ({
    loaded: false,
    chartData: "",
    radarChartOptions: {
      responsive: true,
      maintainAspectRatio: false,
      scales: {
        r: {
          suggestedMax: 50,
          suggestedMin: 0,
          stepSize: 5,
        },
      },
    },
  }),

  methods: {
    getStudentData() {
      axios
        .get(`http://localhost:3001/avg?name=${this.selectedStudentName}`)
        .then((res) => {
          const { ttlgrScore, ttlvcScore, ttllgScore, ttlrdScore, 
                    ttlcls1Score, ttlcls2Score, ttlcls3Score, ttlalgScore, ttlmltScore, ttlengnScore  } =
            res.data[0];

          console.log("radar.vue selectedStudentName data: ", res.data);
          console.log("ttlgrscore, ", ttlgrScore);

          this.updateChartData(ttlgrScore, ttlvcScore, ttllgScore, ttlrdScore,);
          this.updateMathChartData(ttlcls1Score, ttlcls2Score, ttlcls3Score, ttlalgScore, ttlmltScore, ttlengnScore);
        })
        .catch((error) => {
          console.log("error: ", error);
        });
    },

    updateChartData(ttlgrScore, ttlvcScore, ttllgScore, ttlrdScore) {
      this.chartData = {
        labels: ["문법", "어휘", "논리", "독해"],
        datasets: [
          {
            label: this.selectedStudentName,
            data: [ttlgrScore, ttlvcScore, ttllgScore, ttlrdScore],
            fill: true,
            backgroundColor: "rgba(54, 162, 235, 0.2)",
            borderColor: "rgb(54, 162, 235)",
            pointBackgroundColor: "rgb(54, 162, 235)",
            pointBorderColor: "#fff",
            pointHoverBackgroundColor: "#fff",
            pointHoverBorderColor: "rgb(54, 162, 235)",
          },
          {
            label: "전체 평균",
            // data: [ttlgrScore, ttlvcScore, ttllgScore, ttlrdScore],
            data: [11.6, 5.5, 7.3, 27.4],
            fill: true,
            backgroundColor: "rgba(255, 99, 132, 0.2)",
            borderColor: "rgb(255, 99, 132)",
            pointBackgroundColor: "rgb(255, 99, 132)",
            pointBorderColor: "#fff",
            pointHoverBackgroundColor: "#fff",
            pointHoverBorderColor: "rgb(255, 99, 132)",
          },
        ],
      };
      this.loaded = true;
    },

    updateMathChartData(ttlcls1Score, ttlcls2Score, ttlcls3Score, ttlalgScore, ttlmltScore, ttlengnScore) {
      this.mathChartData = {
        labels: ["미적분1", "미적분2", "미적분3", "선형대수", "다변수", "공학수학"],
        datasets: [
          {
            label: this.selectedStudentName,
            data: [ttlcls1Score, ttlcls2Score, ttlcls3Score, ttlalgScore, ttlmltScore, ttlengnScore],
            fill: true,
            backgroundColor: "rgba(54, 162, 235, 0.2)",
            borderColor: "rgb(54, 162, 235)",
            pointBackgroundColor: "rgb(54, 162, 235)",
            pointBorderColor: "#fff",
            pointHoverBackgroundColor: "#fff",
            pointHoverBorderColor: "rgb(54, 162, 235)",
            datalabels: {
              anchor: "start",
              align: "start",
              offset: "30",
            },
          },
          {
            label: "전체 평균",
            data: [21.7, 14.9, 11.3, 14.7, 5.3],
            fill: true,
            backgroundColor: "rgba(255, 99, 132, 0.2)",
            borderColor: "rgb(255, 99, 132)",
            pointBackgroundColor: "rgb(255, 99, 132)",
            pointBorderColor: "#fff",
            pointHoverBackgroundColor: "#fff",
            pointHoverBorderColor: "rgb(255, 99, 132)",
            datalabels: {
              anchor: "start",
              align: "start",
              offset: "15",
            },
            scales: {
              r: {
                suggestedMax: 100,
                suggestedMin: 0,
                stepSize: 5,
              },
            },
          },
        ],
      };
      this.loaded = true;
    },

  },

  mounted() {
    if (this.selectedStudentName) {
      this.getStudentData();
    }
  },
};
</script>
<style>
.auto-height {
  height: 20rem;
}
</style>